.sideBar {
  background-color: #222;
  height: 100%;
}
.sideBar .tag {
  margin-bottom: 5px;
  margin-left: 0px !important;
  margin-right: 5px;
}
.sideBar .card {
  height: 250px;
  background-color: #222;
  padding: 30px;
  padding-bottom: 0px;
  padding-top: 0px;
  z-index: 9990;
}
.sideBar .card .card_head {
  border-bottom: 1px solid #e8e8e8;
}
.sideBar .card .card_head .title {
  font-size: 16px;
  padding: 16px 0;
  color: #999999;
  font-weight: 500;
  display: inline-block;
  -ms-flex: 1 1;
  flex: 1 1;
}
.sideBar .card .word {
  text-align: center;
}
.sideBar .card .word p {
  color: #999;
  font-size: 13px;
}
.sideBar .card .word .title {
  font-size: 20px;
  font-weight: bold;
  color: #999999;
}
.sideBar .card img {
  width: 50%;
  height: auto;
  margin-top: 20px;
  max-width: 200px;
  max-height: 200px;
  margin-left: 25%;
}
.sideBar .card .friendLink {
  height: 50%;
  padding-top: 15px;
  text-align: left;
}
.sideBar .card .friendLink a {
  cursor: pointer;
  color: #999999;
  padding-right: 10px;
  line-height: 30px;
}
.sideBar .card .icon {
  height: 50%;
  padding-top: 15px;
  text-align: left;
  font-size: 30px;
  color: white;
}
.sideBar .card .icon .iconItem {
  padding-right: 8px;
  cursor: pointer;
}
