.articalCard {
  margin-bottom: 50px;
  display: flex;
  padding: 20px;
  padding-left: 35px;
  flex-direction: column;
  align-items: flex-start;
}
.articalCard .articalCardTitle {
  margin-bottom: 10px;
  font-size: 26px !important;
  font-weight: 400 !important;
  color: #555555 !important;
}
.articalCard .articalCardSubTitle {
  margin-bottom: 20px;
  font-size: 14px;
  color: #999;
  line-height: 2;
}
.articalCard .articalCardSubTitle span {
  margin-right: 10px;
}
.articalCard .articalCardBody {
  width: 100%;
  text-align: left;
}
.articalCard .articalCardfoot {
  margin-top: 20px;
  color: #555555;
  font-size: 14px !important;
  line-height: 2;
  border-bottom: 2px solid #666666;
}
