.main {
  margin-bottom: 20px;
}
.main :global .ant-pagination-item-link {
  border: 1px solid #d9d9d9;
  background-color: #fff;
  border-radius: 4px;
  outline: none;
  display: list-item !important;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 12px;
  text-align: center;
}
.main .articalCard {
  margin-bottom: 50px;
  display: flex;
  padding: 20px;
  padding-left: 35px;
  flex-direction: column;
  align-items: flex-start;
  cursor: pointer;
}
.main .articalCard .articalCardTitle {
  margin-bottom: 10px;
  font-size: 26px !important;
  font-weight: 400 !important;
  color: #555555 !important;
}
.main .articalCard .articalCardSubTitle {
  margin-bottom: 20px;
  font-size: 14px;
  color: #999;
  line-height: 2;
}
.main .articalCard .articalCardSubTitle span {
  margin-right: 10px;
}
.main .articalCard .articalCardBody {
  overflow: hidden;
  width: 100%;
  text-align: left;
  font-size: 14px !important;
  line-height: 2;
  color: #555555 !important;
}
.main .articalCard .articalCardfoot {
  margin-top: 20px;
  color: #555555;
  font-size: 14px !important;
  line-height: 2;
  border-bottom: 2px solid #666666;
}
